import React from 'react';
import { FormGroup, Label, Input } from 'reactstrap';

const Example = () => {
  return (
    <FormGroup check>
      <Label check>
        <Input type="checkbox" />
        Default checkbox
      </Label>
    </FormGroup>
  );
};

export default Example;



